:host {
  display: block;
  width: 100%;
}

.composer {
  display: flex;
  flex-direction: column;
  gap: 0;
  padding: 0;
  border-radius: 0;
  background: transparent;
  border: none;
  border-bottom: 1px solid var(--color-border-light);
  color: var(--color-text-primary);
  transition: all 0.25s ease;
}

.composer--expanded {
  border-bottom: 8px solid var(--color-border-light);
}

.composer__top {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 16px;
}

.avatar {
  flex: 0 0 40px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--color-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 16px;
  color: #ffffff;
}

.composer__input-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.composer__prompt,
.composer__textarea {
  flex: 1;
  border-radius: 0;
  border: none;
  background: transparent;
  color: var(--color-text-secondary);
  font-size: 20px;
  line-height: 1.5;
  transition: all 0.2s ease;
}

.composer__prompt {
  padding: 0;
  text-align: left;
  cursor: pointer;
  color: var(--color-text-secondary);
  font-weight: 400;

  &:hover,
  &:focus-visible {
    outline: none;
  }
}

.composer__textarea {
  min-height: 120px;
  padding: 0;
  resize: vertical;
  background: transparent;
  color: var(--color-text-primary);

  &:focus {
    outline: none;
  }

  &::placeholder {
    color: var(--color-text-secondary);
  }
}


.composer__context {
  margin: 0;
  padding: 0 16px 12px 68px;
  display: flex;
  flex-direction: column;
  gap: 4px;
  font-size: 14px;
  color: var(--color-text-secondary);
}

.composer__name {
  font-weight: 700;
  color: var(--color-text-primary);
}

.composer__title {
  color: var(--color-text-secondary);
}

.composer__hint {
  font-size: 13px;
  color: var(--color-primary);
  font-weight: 400;
}

.composer__divider {
  height: 1px;
  background: var(--color-border-light);
  margin: 0;
}

.composer__bottom {
  display: flex;
  flex-direction: column;
  gap: 0;
}

.composer__actions {
  display: flex;
  gap: 4px;
  padding: 12px 16px;
  border-top: 1px solid var(--color-border-light);
}

.action {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  border-radius: var(--radius-full);
  border: 0;
  background: transparent;
  color: var(--color-primary);
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;

  &:hover,
  &:focus-visible {
    background: var(--color-bg-hover);
    outline: none;
  }

  &--disabled {
    opacity: 0.4;
    cursor: not-allowed;

    &:hover {
      background: transparent;
    }

    &::after {
      content: 'ðŸ”’';
      position: absolute;
      top: 4px;
      right: 4px;
      font-size: 10px;
    }
  }

  &--active {
    background: var(--color-bg-subtle);
    color: var(--color-text-primary);
  }
}

.action__icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
}

.action__icon svg {
  width: 20px;
  height: 20px;
}

.action__label {
  white-space: nowrap;
}

.composer__controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  padding: 12px 16px;
  border-top: 1px solid var(--color-border-light);
}

.composer__action-panel {
  padding: 12px 16px 16px 68px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  background: var(--color-bg-subtle);
  border-top: 1px solid var(--color-border-light);
  border-bottom: 1px solid var(--color-border-light);
}

.panel {
  display: flex;
  flex-direction: column;
  gap: 8px;
  color: var(--color-text-primary);

  h4 {
    margin: 0;
    font-size: 15px;
    font-weight: 700;
  }

  p {
    margin: 0;
    font-size: 14px;
    color: var(--color-text-secondary);
  }

  .btn {
    align-self: flex-start;
  }
}

.panel__actions {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.panel--highlight {
  .highlight-preview {
    border-left: 3px solid var(--color-primary);
    padding-left: 12px;
    display: flex;
    flex-direction: column;
    gap: 6px;

    h5 {
      margin: 0;
      font-size: 16px;
      font-weight: 700;
      color: var(--color-text-primary);
    }

    &__tone {
      font-size: 13px;
      font-weight: 600;
      color: var(--color-primary);
      text-transform: uppercase;
    }

    ul {
      margin: 0;
      padding-left: 18px;
      display: flex;
      flex-direction: column;
      gap: 4px;

      li {
        font-size: 14px;
        color: var(--color-text-secondary);
      }
    }
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
  padding: 8px 16px;
  border-radius: var(--radius-full);
  font-size: 15px;
  font-weight: 700;
  border: 0;
  transition: background 0.2s ease;
  cursor: pointer;
}

.btn--ghost {
  color: var(--color-text-primary);
  background: transparent;
  border: 1px solid var(--color-border-light);

  &:hover,
  &:focus-visible {
    background: var(--color-bg-hover);
    outline: none;
  }
}

.btn--primary {
  background: var(--color-primary);
  color: #ffffff;

  &:hover,
  &:focus-visible {
    background: var(--color-primary-hover);
    outline: none;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

// Estilos para treinos selecionados
.selected-workouts {
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  background: #f8f9fa;
  margin-top: 8px;

  .workouts-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 16px;
    border-bottom: 1px solid #e0e0e0;
    background: #f1f3f4;

    .workouts-icon {
      color: #1976d2;
      font-size: 20px;
      width: 20px;
      height: 20px;
    }

    .workouts-title {
      flex: 1;
      font-weight: 600;
      color: #333;
      margin-left: 8px;
    }

    .clear-workouts-btn {
      mat-icon {
        color: #666;
        font-size: 18px;
        width: 18px;
        height: 18px;
      }
    }
  }

  .workouts-list {
    max-height: 200px;
    overflow-y: auto;

    .workout-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 12px 16px;
      border-bottom: 1px solid #f0f0f0;

      &:last-child {
        border-bottom: none;
      }

      &:hover {
        background: #f0f7ff;
      }

      .workout-info {
        display: flex;
        align-items: center;
        gap: 12px;
        flex: 1;

        .workout-type-icon {
          color: #1976d2;
          font-size: 20px;
          width: 20px;
          height: 20px;
        }

        .workout-details {
          flex: 1;

          .workout-name {
            font-weight: 600;
            color: #333;
            font-size: 0.95rem;
            margin-bottom: 4px;
          }

          .workout-meta {
            display: flex;
            align-items: center;
            gap: 8px;
            font-size: 0.85rem;
            color: #666;

            .workout-type {
              font-weight: 500;
              color: #1976d2;
            }

            .workout-date,
            .workout-distance {
              color: #888;
            }
          }
        }
      }

      .remove-workout-btn {
        mat-icon {
          color: #f44336;
          font-size: 18px;
          width: 18px;
          height: 18px;
        }
      }
    }
  }
}

// Estilo para aÃ§Ã£o de treinos ativa
.action--active {
  background: #e3f2fd !important;
  border-color: #1976d2 !important;

  .workout-action-icon {
    color: #1976d2 !important;
  }

  .action__label {
    color: #1976d2 !important;
  }
}

.action-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 20px;
  height: 20px;
  padding: 0 6px;
  border-radius: 10px;
  background: #1976d2;
  color: white;
  font-size: 0.75rem;
  font-weight: 600;
  margin-left: 4px;
}

.workout-action-icon {
  font-size: 26px;
  width: 26px;
  height: 26px;
}

@media (max-width: 600px) {
  .composer {
    padding: 0;
  }

  .composer__top {
    padding: 12px;
  }

  .composer__context {
    padding-left: 52px;
  }

  .composer__actions {
    flex-wrap: wrap;
  }

  .composer__controls {
    padding: 12px;
  }

  .selected-workouts {
    .workouts-header {
      padding: 10px 12px;

      .workouts-title {
        font-size: 0.9rem;
      }
    }

    .workouts-list {
      .workout-item {
        padding: 10px 12px;

        .workout-info {
          gap: 10px;

          .workout-details {
            .workout-name {
              font-size: 0.9rem;
            }

            .workout-meta {
              font-size: 0.8rem;
              flex-wrap: wrap;
              gap: 6px;
            }
          }
        }
      }
    }
  }
}
